
import { defineComponent, ref, reactive } from "vue";
import Alerta from "@/packages/alerta";
import VueRecaptcha from "vue-recaptcha";
import Loading from "../Loading.vue";
// import { ValidationProvider } from "vee-validate/dist/vee-validate.full.min";
// import { ValidationObserver, ValidationProvider } from "vee-validate";

export default defineComponent({
  name: "ContactoComponent",
  setup() {
    const fishing = ref("");
    const contacto = reactive({
      nombre: "",
      email: "",
      asunto: "",
      mensaje: "",
    });
    const error = reactive({
      nombre: false,
      email: false,
      asunto: false,
      mensaje: false,
    });
    const aviso = ref(false);
    const rcaptchaSigKey = ref("6LeqkcsZAAAAAMcRTTo10qgTN0w_V34IzsGz7HHl");

    const validate = () => {
      error.nombre = false;
      error.email = false;
      error.asunto = false;
      error.mensaje = false;
      if (contacto.nombre === "") {
        error.nombre = true;
        return false;
      }
      if (contacto.email === "") {
        error.email = true;
        return false;
      }
      if (contacto.asunto === "") {
        error.asunto = true;
        return false;
      }
      if (contacto.mensaje === "") {
        error.mensaje = true;
        return false;
      }
      if (fishing.value !== "") {
        return false;
      }
      return true;
    };

    const onCaptchaVerify = (token: string | undefined | null) => {
      if (validate()) {
        aviso.value = true;
        const verify = { response: token };
        const $alerta = new Alerta();
        if (!token) {
          $alerta.error("Captcha", "No pasó el desafío Recaptcha");
          aviso.value = false;
        } else {
          const datos = { contacto: contacto, captcha: verify };
          fetch(`${process.env.VUE_APP_LARAVEL_API_URL}api/mails/contacto`, {
            method: "POST",
            body: JSON.stringify(datos),
            headers: {
              "Content-Type": "application/json",
            },
          })
            .then((response) => response.json())
            .then((response) => {
              $alerta.success(
                "Enviado!",
                "El mensaje fue enviado correctamente"
              );
              aviso.value = false;
            })
            .catch((err) => {
              $alerta.error(
                "Hay errores",
                "Error al enviar, intentelo nuevamente"
              );
              aviso.value = false;
            });
        }
      }
    };
    const enviar = () => {
      // window.grecaptcha.ready(() => {
      //   window.grecaptcha.execute(rcaptchaSigKey.value);
      // });
    };

    return {
      contacto,
      error,
      aviso,
      rcaptchaSigKey,
      fishing,
      //methods
      onCaptchaVerify,
      enviar,
    };
  },

  components: {
    Loading,
    // VueRecaptcha
    // ValidationObserver,
    // ValidationProvider
  },
});
